import LinkBox from "./linkBox"
import PictureFrame from "./pictureFrame"

import { useContext } from "react"
import {WebContext} from "../App"

import Fade from 'react-reveal/Fade';

export default function Mosaic(props){
    const rightLinkBoxID = `${props.id}-Rl`
    const rightPictureFrameID = `${props.id}-Rp`
    const leftLinkBoxID = `${props.id}-Ll`
    const leftPictureFrameID = `${props.id}-Lp`

    const webContext = useContext(WebContext);



    if (props.webStyle.isMobile){
        return(
        <div className = {"row "+(webContext.webStyle.isMobile?"px-2 ":"px-5")} data-no-dnd="true">
            <div className="col">
                <div className = {"row g-0"}>
                    <Fade bottom>
                        <div className="row g-0 mb-5">
                            <PictureFrame  webStyle = {webContext.webStyle} content = {props.content.lPicContent} key = {leftPictureFrameID} id = {leftPictureFrameID} isNested/>
                        </div>
                    </Fade>
                    <Fade bottom>
                        <LinkBox key = {leftLinkBoxID} id = {leftLinkBoxID} content = {props.content.lLinkBoxContent} webStyle = {webContext.webStyle} adminProps = {props.adminProps}/>
                    </Fade>
                </div>
                <div className = {"row mb-5 g-0"}>
                    <Fade bottom>
                        <div className="row g-0 mb-5">
                            <PictureFrame webStyle = {webContext.webStyle} content = {props.content.rPicContent} key = {rightPictureFrameID} id = {rightPictureFrameID} isNested/>
                        </div>
                    </Fade>

                    <Fade bottom>
                        <LinkBox key = {rightLinkBoxID} id = {rightLinkBoxID} content = {props.content.rLinkBoxContent} webStyle = {webContext.webStyle} adminProps = {props.adminProps}/>
                    </Fade>

                </div>
            </div>
        </div>)
    }
    else{
        return(
            <div className = {"row g-0 px-5"} data-no-dnd="true">
                <div className = {"col me-3"}>
                    <Fade bottom>

                        <div className="row g-0 mb-5 w-100">
                            <PictureFrame  webStyle = {webContext.webStyle}  content = {props.content.lPicContent} key = {leftPictureFrameID} id = {leftPictureFrameID} isNested/>
                        </div>
                            <LinkBox key = {leftLinkBoxID} id = {leftLinkBoxID} content = {props.content.lLinkBoxContent} webStyle = {webContext.webStyle} adminProps = {props.adminProps}/>
                    </Fade>

                </div>
                <div className = {"col ms-3"}>
                    <Fade bottom>

                        <div className="row g-0 mb-5">
                            <LinkBox key = {rightLinkBoxID} id = {rightLinkBoxID} content = {props.content.rLinkBoxContent} webStyle = {webContext.webStyle} adminProps = {props.adminProps}/>
                        </div>
                            <PictureFrame webStyle = {webContext.webStyle} content = {props.content.rPicContent} key = {rightPictureFrameID} id = {rightPictureFrameID} isNested/>
                    </Fade>

                </div>
                
            </div>
        )
    }
}