import { useState } from "react";
import DynamicForm from "./dynamicForm";
import QuillComponent from "./quillComponent";

export default function EmailSender(props) {


    return (
    <div className="mb-4 px-5 mt-3 text-start" >
        <div className="g-0 boxShadow" style={{backgroundColor:props.webStyle.lightShade}}>
            <QuillComponent className = "paragraph px-3 pt-3"  webStyle = {props.webStyle} id ={props.id} content = {props.content} />      
            
            <DynamicForm emailSendor/>
        </div>
    </div>
  );
}
